@charset "UTF-8";
.smallProductTitle {
  display: flex;
  padding: 30px; }
  .smallProductTitle .cost::before {
    font-family: Arial, Helvetica, sans-serif;
    content: "₹";
    display: inline-block;
    padding-right: 3px;
    font-weight: normal; }
  .smallProductTitle .smallProductTitle-left {
    width: 120px;
    text-align: center;
    display: flex;
    flex-direction: column;
    margin-right: 30px; }
  .smallProductTitle .product-name {
    font-weight: bold;
    font-size: 1.1rem; }
  .smallProductTitle img {
    border-radius: 4px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2); }
