@charset "UTF-8";
.mobileProductTile {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  background: #ffffff;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding: 20px 20px 20px 10px;
  margin-right: 0px;
  margin-bottom: 1em; }
  .mobileProductTile a {
    text-decoration: none; }
  .mobileProductTile .productImage img {
    transform: scale(0.95);
    transition: all 0.5s ease;
    flex: 1;
    max-width: 120px; }
    .mobileProductTile .productImage img:hover {
      transform: scale(1); }
  .mobileProductTile .productInfo {
    display: flex;
    flex: 1;
    flex-direction: column;
    font-size: 14px; }
    .mobileProductTile .productInfo .button {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 0;
      width: 150px;
      margin-left: auto;
      font-size: 14px;
      padding: 6px 15px;
      height: 38px; }
  .mobileProductTile .productExtraInfo {
    display: flex;
    justify-content: flex-end; }
    .mobileProductTile .productExtraInfo strong {
      font-size: 1rem; }
      .mobileProductTile .productExtraInfo strong.title {
        margin-left: 15px;
        background: #f28c14;
        color: #fff;
        padding: 6px;
        font-weight: 400;
        font-size: 12px;
        line-height: 1;
        border-radius: 4px;
        box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2); }
  .mobileProductTile .cost {
    font-size: 1rem;
    margin-left: auto;
    padding-top: 20px; }
    .mobileProductTile .cost span::before {
      font-family: Arial, Helvetica, sans-serif;
      content: "₹";
      display: inline-block;
      padding-right: 5px; }
