@charset "UTF-8";
.checkoutPage .cost::before {
  font-family: Arial, Helvetica, sans-serif;
  content: "₹";
  display: inline-block;
  padding-right: 3px;
  font-weight: normal; }

.checkoutPage .cost1::before {
  font-family: Arial, Helvetica, sans-serif;
  content: "₹";
  display: inline-block;
  padding-right: 0px;
  font-weight: normal; }

.checkoutPage a {
  display: inline-block;
  text-decoration: none; }

.checkoutPage .button {
  font-size: 14px;
  padding: 6px 15px;
  height: 38px;
  width: 150px;
  text-align: center; }
  .checkoutPage .button.button-small {
    text-transform: uppercase;
    font-size: 10px;
    height: 23px;
    width: auto;
    padding: 2px 6px;
    margin-top: 5px;
    margin-bottom: 0; }

.checkoutPage.emptyCartMessage {
  text-align: center; }
  .checkoutPage.emptyCartMessage h1 {
    padding: 50px 0 30px; }

.checkoutPage .smallProductTitle-container {
  display: flex; }
  @media (max-width: 767px) {
    .checkoutPage .smallProductTitle-container {
      flex-direction: column; } }

.checkoutPage .order-summary-container {
  text-align: right;
  padding: 20px;
  width: 400px;
  margin: 0 auto;
  flex-direction: column; }
  .checkoutPage .order-summary-container .content {
    padding: 0 0 5px; }
    .checkoutPage .order-summary-container .content span {
      display: inline-block;
      padding-left: 4px;
      font-size: 13px;
      color: #a2a1a1; }
  @media (max-width: 767px) {
    .checkoutPage .order-summary-container {
      width: 100%;
      text-align: left; }
      .checkoutPage .order-summary-container .content {
        flex-direction: row; }
        .checkoutPage .order-summary-container .content .col-6.col-6-r {
          width: 40%;
          text-align: right; } }
  .checkoutPage .order-summary-container .delivery-fee {
    font-size: 14px;
    padding-top: 5px; }
  .checkoutPage .order-summary-container .total-pay {
    padding-top: 10px;
    font-weight: bold;
    border-top: 1px #f0f0f0 solid;
    margin-top: 10px; }

.checkoutPage .userDetailsForm-container {
  padding: 30px 30px 0; }

.checkoutPage .actions {
  border-top: 1px solid #f0f0f0;
  display: flex;
  justify-content: flex-end;
  padding: 0 20px; }
  .checkoutPage .actions .button {
    margin-left: 10px; }

.checkoutPage .white-box {
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
  margin-bottom: 20px; }
  .checkoutPage .white-box .white-box-content {
    max-height: 1000px;
    animation-delay: 0.5s;
    overflow: hidden; }
    .checkoutPage .white-box .white-box-content.hide {
      max-height: 0; }
  .checkoutPage .white-box .cart-title {
    padding: 15px 20px;
    border-bottom: 1px #f0f0f0 solid;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .checkoutPage .white-box .cart-title h2 {
      padding: 10px 0;
      font-size: 20px;
      font-weight: bold;
      line-height: 1;
      margin: 0; }
    .checkoutPage .white-box .cart-title .button {
      margin: 0; }

.checkoutPage .price-details-container {
  position: sticky;
  top: 120px; }
  .checkoutPage .price-details-container .cart-title {
    border-bottom: 1px solid #f0f0f0; }
  .checkoutPage .price-details-container .price-details {
    padding: 15px 20px; }
    .checkoutPage .price-details-container .price-details div {
      display: flex;
      justify-content: space-between;
      padding-bottom: 10px;
      font-size: 14px; }
      .checkoutPage .price-details-container .price-details div.totalpay {
        margin-top: 30px;
        font-weight: 700;
        padding-top: 15px;
        border-top: 1px #343a41 solid; }
