@charset "UTF-8";
.productTile {
  border-top: 6px solid #f28c14;
  box-shadow: 0px 40px 20px -40px #858585;
  background: #ffffff;
  padding: 20px;
  margin: 0 20px; }
  .productTile a {
    text-decoration: none; }
  .productTile .productImage img {
    transform: scale(0.8);
    transition: all 0.5s ease;
    flex: 1; }
    .productTile .productImage img:hover {
      transform: scale(1); }
  .productTile .productInfo {
    font-size: 1.1rem;
    display: flex;
    flex-direction: column;
    text-align: center; }
    .productTile .productInfo .button {
      height: 42px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 250px;
      text-align: center;
      margin-left: auto;
      margin-right: auto; }
  .productTile .title {
    display: block; }
  .productTile .cost {
    display: block;
    font-size: 1.6rem;
    padding-top: 0.8em; }
    .productTile .cost::before {
      font-family: Arial, Helvetica, sans-serif;
      content: "₹";
      display: inline-block;
      padding-right: 5px; }
